	module.exports = {"data":{"markdownRemark":{"html":"<p>The inequality operator (<code>!=</code>) is the opposite of the equality operator. It means “Not Equal” and returns <code>false</code> where equality would return <code>true</code> and vice versa. Like the equality operator, the inequality operator will convert data types of values while comparing.</p>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>1 != 2      // true\n1 != \"1\"    // false\n1 != '1'    // false\n1 != true   // false\n0 != false  // false</code></pre>\n      </div>","fields":{"slug":"/certificates/comparison-with-the-inequality-operator/"},"frontmatter":{"title":"Comparison with the Inequality Operator"}}},"pathContext":{"slug":"/certificates/comparison-with-the-inequality-operator/"}}

